.hero-about {
  padding-top: 50px;

  &__box {
    position: relative;
    max-width: 1169px;
    width: 100%;
    height: 250px;

    overflow: hidden;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #2a2a2a;
    background-image: url(../../img/k1170gr250.png);
    background-size: cover;
    background-position: center center;

  }

  &__overlay {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
  }

  &__title {
    padding-top: 40px;
    font-weight: 700;
    font-size: 60px;
    color: antiquewhite;
    text-align: center;

  }
}

