.hero-catalog {

  &__slider {
    margin-top: 0px;
    margin-bottom: 0px;
    position: relative;
    overflow: hidden;
    max-width: 1190px;
    width: 100%;
    height: 290px;
    background-color: #f8f8f8;

  }
}

.catalog-slide {
  position: relative;
  padding: 0px 0px;
  display: flex;

  justify-content: center;
  align-items: center;

  @include sm {
    padding: 20px 20px;
  }

  @include xs {
    padding: 10px 10px;
  }

  &__title {
    max-width: 375px;
    width: 100%;
    font-weight: 700;
    font-size: 40px;
    z-index: 10;
  }

  &__d {
    margin: 0;
    margin-bottom: 40px;
    max-width: 400px;
    width: 100%;
    font-weight: 500;
    font-size: 14px;
    color: black;
    z-index: 10;
  }

  &__btns {
    display: flex;
    align-items: center;
    flex-wrap: wrap;

    z-index: 10;
  }

  &__link {
    display: inline-block;
    font-size: 12px;

    padding: 15px 45px;
    margin-top: 10px;

    &--dark {
      border: 2px solid var(--color-dark);
      color: var(--color-white);
      background-color: var(--color-dark);

      &:hover {
        border: 2px solid var(--color-dark);
        color: var(--color-dark);
        background-color: transparent;
        transition: all 0.3s;
      }
    }

    &--light {
      border: 2px solid var(--color-dark);
      color: var(--color-dark);
      background-color: transparent;
      transition: all 0.3s;

      &:hover {
        border: 2px solid var(--color-dark);
        color: var(--color-white);
        background-color: var(--color-dark);
      }
    }

    &:not(:last-child) {
      margin-right: 15px;
    }
  }

  &__image {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    max-width: 1190px;
    width: 100%;
    min-height: 290px;
    object-fit: contain;
    z-index: 10;

    @include xs420 {
      max-height: 60%;

    }
  }
}

.blog-prev-btn {
  position: absolute;
  left: 10px;
  top: 50%;
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid var(--color-white);
  width: 48px;
  height: 48px;
  color: var(--color-dark);
  transform: translateY(-50%);
  transition: background-color 0.3s;

  svg {
    width: 40px;
    height: 20px;

    background-image: url(../../img/prev-arrow-white.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    transition: fill 0.3s;

    &:hover {
      path {
        fill: #000;
      }

      transition: background-color 0.3s;

    }
  }

  &:hover {
    background-color: var(--color-white);
    background-image: url(../../img/prev-arrow.svg);
    transition: background-color 0.3s;

  }

}

