.hero-pag {
  left: 50%;
  transform: translateX(-50%);
  bottom: 10px;
  text-align: center;
  z-index: 1500;
}

.swiper-pagination-bullet {
  border-radius: 0;
  margin: 0 5px !important;
  z-index: 1500;
  opacity: 1;
  width: 16px;
  height: 6px;
  border: none;
  background-color: var(--color-white);
}

.swiper-pagination-bullet-active {
  position: relative;
  background-color: var(--color-dark);
  z-index: 1500;
  opacity: 1;


}

.search__tabs {
  margin-bottom: 25px;

  @include sm {

    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .search__content-form,
    .mobile__overflow {
      flex-direction: column;
    }
  }
}

.mobile__overflow {
  display: flex;
}

.search__tabs-item {

  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  max-width: 290px;
  width: 100%;
  height: 50px;
  color: #2F3035;
  padding: 10px 10px;
  margin-bottom: 5px;
}

.search__tabs-item--active {
  max-width: 290px;
  width: 100%;
  height: 47px;


  background: #F0F0F4;
  border-radius: 5px;
}

.search__content-form {
  display: flex;

}

.search__content-btn {
  width: 145px;
  height: 50px;
  padding: 17px 40px;
  background: cadetblue;
  border-radius: 3px;

  font-style: normal;
  font-weight: 700;
  font-size: 10px;
  line-height: 15px;


  letter-spacing: 0.12em;
  text-transform: uppercase;

  color: #FFFFFF;
  border: none;

}

.search__content-btn:hover {
  background: black;
  transition: all 0.4s;
}

.search__content-input {
  max-width: 1170px;
  width: 100%;
  height: 50px;
  padding: 5px 10px;
  margin-bottom: 5px;
  background: #F0F0F4;
  border-radius: 3px;

  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;

  color: #656464;
  border: none;
}

.search__content-input::placeholder {
  width: 1170px;
  height: 50px;
  padding: 5px 10px;

  background: #F0F0F4;


  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;

  color: #646464;

}

.search__content-item {
  display: none;
}

.search__content-item--active {
  display: block;
}
